@import url(https://fonts.googleapis.com/css2?family=DM+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Eagle+Lake&display=swap);
body {
  margin: 0;
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  height: 100%;
}

.container{height:100%;display:flex;flex-flow:column nowrap;justify-content:space-between;background-color:#e5e5e5;font-family:'DM Mono', monospace}.container .main{display:flex;flex-flow:row nowrap;justify-content:start}.container .main .side-menu{margin-right:2em;padding:1em}.container .main .content{width:100%;padding:2em}

#hero{display:flex;justify-content:center;align-items:center;height:20rem;width:100vw;background-color:#080705;color:#E5E5E5}

.side-menu{display:flex;flex-direction:column;align-items:start;margin:0 1rem;width:15%;list-style-type:none;opacity:0.95;color:#8a4f7d}.side-menu h1{font-family:'Eagle Lake', cursive;margin-bottom:5rem;margin-top:0}.side-menu .menu-link{display:flex;flex-flow:row nowrap;margin:0 0 30px 10px;text-decoration:none;font-size:1.5em;font-family:"Raleway", sans-serif}.side-menu .menu-link:link,.side-menu .menu-link:active,.side-menu .menu-link:visited,.side-menu .menu-link:hover{color:#8a4f7d}.side-menu .menu-link .hover{text-align:center;margin:0 auto;padding:0;transition:all 0.2s ease-in-out;position:relative}.side-menu .menu-link .hover:before,.side-menu .menu-link .hover:after{content:"";position:absolute;bottom:-8px;width:0px;height:2px;transition:all 0.2s ease-in-out;transition-duration:0.5s;opacity:0;background-color:#7a466e}.side-menu .menu-link .hover.hover-center:before{left:50%}.side-menu .menu-link .hover.hover-center:after{right:50%}.side-menu .menu-link:hover{cursor:pointer}.side-menu .menu-link:hover .hover:before,.side-menu .menu-link:hover .hover:after{width:100%;opacity:1}.side-menu .menu-link:hover .hover.hover-center:before,.side-menu .menu-link:hover .hover.hover-center:after{width:50%}

#footer{display:flex;justify-content:space-evenly;height:3rem;background-color:#080705;color:#e5e5e5}#footer a{margin:auto 0;text-decoration:none;text-shadow:1px 1px 4px #e5e5e5}#footer a:hover,#footer a:active,#footer a:link,#footer a:visited{color:#e5e5e5}

.content{margin-left:3rem}.content h2{text-align:center;padding:0;font-size:2.4rem;transform:translate(-10%)}.content h2:first-child{margin:0 0 3rem 0}.content ul.technologies{display:flex;flex-flow:row wrap;margin:0;padding:0}.content ul.technologies li{display:flex;flex-flow:column nowrap;justify-content:center;align-items:center;margin:1em;padding:10px;height:6rem;width:6rem;box-shadow:1px 2px 3px 2px #c7c6c6;background-color:#f0f0f0}.content ul.technologies h5{margin:15px 0}.content ul.CV{display:flex;justify-content:center;transform:translate(-10%);margin:2rem 0 0 0}.content ul.CV li.curriculum{margin:2rem;list-style-type:none}.content ul.CV li.curriculum a{display:flex;flex-flow:column nowrap;align-items:center;justify-content:center;text-decoration:none}.content ul.CV li.curriculum a:link,.content ul.CV li.curriculum a:active,.content ul.CV li.curriculum a:visited,.content ul.CV li.curriculum a:hover{color:#3891a6}.content ul.CV li.curriculum a img{width:200px;height:200px;border:1px solid #3891a6}.content .projects{display:flex;flex-flow:row wrap;justify-content:space-around}.content .projects .project{margin:1rem;padding:1.2rem;list-style-type:none;border:1px solid #3891a6;border-radius:5px;box-shadow:1px 1px 3px 0px #3891a6}.content .projects .project a{text-decoration:none;color:#080705}.content .projects .project h4{margin-top:0;color:#3891a6}.content .projects .project:hover{background-color:#fff;transition:0.5s}

#contact #socialmedia{display:flex;justify-content:center}#contact #socialmedia a{margin:0 1rem}#contact #socialmedia a:link,#contact #socialmedia a:active,#contact #socialmedia a:visited,#contact #socialmedia a:hover{color:#3891a6}#contact h4 span{color:#3891a6;text-decoration:underline}#contact form{display:flex;flex-flow:column nowrap}#contact form .button{display:flex;justify-content:center;margin-top:0.8rem}#contact form .button button{padding:3px;width:25%;border:1px solid #3891a6;border-radius:5px}

